import React from 'react'
import { Link as GLink } from 'gatsby'
import { Heading } from 'theme-ui'
import rv from '@components/utils/buildResponsiveVariant'
import { useTranslation } from "react-i18next"
const {localizedUrl} = require("../../../../../site/src/util")

const CardBodyTitle = ({ variant, title, slug, link }) => {
  const {i18n} = useTranslation()

  const linkProps = link
    ? {
        as: 'a',
        href: link,
        target: '_blank',
        rel: 'noopener noreferrer'
      }
    : {
        as: GLink,
        to: localizedUrl({ url: slug, locale: i18n.language }) 
      }
  return (
    <Heading {...linkProps} sx={{ variant: rv(variant, 'title') }}>
      {title}
    </Heading>
  )
}

export default CardBodyTitle
